.dropdown_container {
    border-radius: 5px;
    float: left;
    width: 100px;
    height: 20px;
    position: relative;
    border: 1px solid #ccc;
    margin: 5px 10px 5px 10px;
}

.dropdown_menu {
    position: absolute;
    transform: translateY(2px);
    width: 100%;
    border: 1px solid #ccc;
    border-radius: 5px;
    overflow: auto;
    max-height: 150px;
    background-color: #f1f2f3;
}

.dropdown_item {
    padding: 5px;
    cursor: pointer;
}


.dropdown_container{
    z-index: 999;
}

.dropdown_item:hover {
    background-color: #a2e5d0;
}

.dropdown-input {
    padding: 5px;
    display: flex;
    align-items: center;
    justify-content: space-between;
    user-select: none;
}

.menu-container{
    margin: auto
}
